const injectHtmlLang = require('./inject/htmlLang');
const injectMetas = require('./inject/metas');
const injectStyles = require('./inject/styles');
const injectScripts = require('./inject/scripts');

/**
 *  ejs 
 * @param {Object} options 
 * @param {Object} [options.injectCache={}] /
 * @param {Object} [options.filemap={}] () 
 * @param {Object} [options.entrypoints={}] () 
 * @param {String} [options.localeId]  ID
 * @param {Object} [options.localeFileMap] (SPA) 
 * @param {string} [options.defaultLocaleId] (SPA)  ID
 * @param {String} [options.title] 
 * @param {String} [options.metaHtml] meta  HTML 
 * @param {String} [options.reactHtml]  React  HTML 
 * @param {String} [options.stylesHtml]  HTML 
 * @param {String} [options.reduxHtml]  redux store  HTML 
 * @param {Object} [options.SSRState] SSR 
 * @param {Object} [options.needInjectCritical]  critical 
 * @param {Boolean} [options.needInjectCritical.styles=false]
 * @param {Boolean} [options.needInjectCritical.scripts=false]
 * @returns {Object}
 */
module.exports = (options = {}) => {
    const {
        injectCache = {},

        manifest = {},
        filemap = {},
        entrypoints = {},
        compilation,

        localeId,
        localeFileMap,
        defaultLocaleId,

        title,
        metaHtml,
        reactHtml,
        stylesHtml,
        reduxHtml,
        SSRState,

        needInjectCritical = {
            styles: false,
            scripts: false,
        },
    } = options;

    return {
        htmlLang: injectHtmlLang(localeId),
        title,
        metas: injectMetas({ manifest, metaHtml, localeId, compilation }),
        styles: injectStyles({
            needInjectCritical: needInjectCritical.styles,
            injectCache,
            filemap,
            stylesHtml,
            localeId,
            compilation,
        }),

        react: reactHtml,

        scripts: injectScripts({
            needInjectCritical: needInjectCritical.scripts,
            injectCache,
            entrypoints,
            localeId,
            localeFileMap,
            defaultLocaleId,
            reduxHtml,
            SSRState,
            compilation,
        }),
    };
};
